<template lang="html">
<transition name="showRouter">
    <div class="flex">
        <div class="header clearfix">
            <p class="title">歡歌唱唱</p>
            <img v-bind:src="playlist.image || require('../../assets/playlist.png')" class="cover"/>
            <ul class="info">
                <li class="name">{{ playlist.name }}</li>
                <li class="user">{{ playlist.owner?playlist.owner.nick:'' }}</li>
                <li class="desc">{{ playlist.desc }}</li>
            </ul>
        </div>
        <div class="music-list">
            <div v-for="(item, index) in songList" :key='index' class="music-item">
                <img v-bind:src="item.image" class="music-img" />
                <span class="music-name">{{ (index+1) + '.&nbsp; ' + item.name}}</span>
            </div>
        </div>
        <div class="footer clearfix">
            <p class="button open" @click="openApp">打開</p>
            <p class="button download" @click="downloadApp">下載</p>
        </div>
    </div>

</transition>
</template>

<script>
export default {
  name: 'MusicList',
  props: ['pid'],
  created () {
    this.getPlaylist()
  },
  data () {
    return {
      playlist: {},
      songList: []
    }
  },
  methods: {
    getPlaylist: function () {
      this.$http
        .get(
          config.serverUrl + '/web-playlist', {params: {
            pkg: config.packageName,
            pid: this.pid
          }}
        )
        .then(
          res => {
            console.log(res.body)
            this.playlist = res.body.playlist
            this.songList = res.body.songList
          },
          () => {
            console.log('error')
          }
        )
    },
    openApp: function () {
      console.log('openApp' + this.pid)
      window.location = config.packageName + '://playlist/' + this.pid
    },
    downloadApp: function () {
      window.location = 'https://play.google.com/store/apps/details?id=' + config.packageName
    }
  }
}
</script>

<style lang="scss">
.showRouter-enter-active {
  transition: all 0.4s ease;
}

.showRouter-leave-active {
  transition: all 0 ease;
}

.showRouter-enter,
.showRouter-leave-active {
  transform: translateX(-150px);
  opacity: 0;
}

.flex {
  flex: 8;
}

.header {
  background: -webkit-linear-gradient(left, #fd9f55, #ff5079);
  /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(right, #fd9f55, #ff5079);
  /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(right, #fd9f55, #ff5079);
  /* Firefox 3.6 - 15 */
  background: linear-gradient(to right, #fd9f55, #ff5079);
  /* 标准的语法（必须放在最后） */

  .title {
    color: #fff;
    padding-top: 10px;
    text-align: center;
  }

  .cover {
    width: 80px;
    height: 80px;
    float: left;
    margin-left: 20px;
    margin-bottom: 10px;
    border-radius: 5px;
    margin-right: 10px;
  }

  .info {
    text-align: left;
    list-style: none;
    color: #fff;
  }

  .info .name {
    padding-top: 5px;
    font-size: 18px;
  }

  .info .user {
    margin-top: 5px;
    font-size: 14px;
    opacity: 0.9;
  }

  .info .desc {
    margin-top: 2px;
    font-size: 14px;
    opacity: 0.9;
  }
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.footer {
  position: fixed;
  background-color: #222;
  bottom: 0px;
  left: 0px;
  width: 100%;
  z-index: 9999;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;

  .button {
    border: 2px solid #ff5079;
    border-radius: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    display: inline-block;
  }

  .open {
    width: 40%;
    background: #fff;
    color: #ff5079;
    float: left;
  }

  .download {
    width: 40%;
    background: #ff5079;
    color: #fff;
  }
}

.music-list {
  padding-top: 4px;
  padding-left: 4px;
  padding-right: 4px;
  margin-bottom: 85px;
  overflow: auto;

  .music-item + .music-item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  .music-item {
    // box-shadow: 0 0 1px #DD2C00;
    padding: 4px 6px 0 6px;
    position: relative;
    margin-bottom: 4px;
    border-radius: 5px;
    cursor: pointer;
    border: none;

    .music-img {
      width: 50px;
      height: 50px;
      border-radius: 5px;
    }

    span.music-name {
      display: inline-block;
      width: 70%;
      vertical-align: top;
      margin-top: 15px;
      margin-left: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: left;
      cursor: pointer;
      padding-bottom: 20px;
    }
  }
}
</style>



